import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cart:[],
    allChecked:false
  },
  mutations: {
    add(state,arr){
      state.cart.push(arr)
    },
    addIndex(state,i){
      state.cart[i].number++
    },
    // 加的方法
    addshop(state,i){
      state.cart[i].number++
    },
    // 减去一个商品
    jian(state,i){
      if(state.cart[i].number--<=1){
        if(confirm('您确定要是删除吗?')){
          state.cart.splice(i,1)
        }else{
          state.cart[i].number=1
          return false
        }
      }
    },
    // 点击复选框
    check(state,i){
      state.cart[i].bool = !state.cart[i].bool
      let bool = state.cart.every((item)=>{
        return item.bool
      })
      state.allChecked = bool
    },
    // 全选按钮
    addBox(state){
      state.allChecked = !state.allChecked
      state.cart.map((m)=>{
        m.bool = state.allChecked
      })
    },
    // 选中删除
    quandel(state,i){
      for(var i=0;i<state.cart.length;i++){
        if(state.cart[i].bool){
          state.cart.splice(i,1)
          i--
        }
        if(state.cart.allChecked==0){
          state.allChecked = false
        }
      }
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    shu({cart}){
      let num = 0
      cart.map((m)=>{
        if(m.bool){
          
          num+=m.number*m.price
        }
      })
      return num
    }
  },
  plugins: [createPersistedState()]
})
