<template>
    <div class="shopCart">
        <header>
            <van-icon name="arrow-left" @click="$router.push('/home/leader')"/>
            <p>购物车</p>
            <div>  </div>
        </header>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <ol class="ol">
                <li v-for="(item,index) in shop" :key="index">
                    <input type="checkbox" :checked="item.bool" @click="addxuan(index)">
                    <div class="img">
                        <img :src="item.pic" alt="">
                    </div>
                    <div class="tet">
                        <p class="list-p1">{{ item.title }}</p>
                        <p class="list-p2">{{ item.price }}</p>
                    </div>
                    <div class="btn">
                        <!-- <van-stepper v-model="item.number" theme="round" button-size="22" disable-input /> -->
                        <van-button type="info" @click="addshop(index)">+</van-button>
                        <span>{{ item.number }}</span>
                        <van-button type="info" @click="open(index)">-</van-button>
                    </div>
                </li>
            </ol>
        </van-pull-refresh>
        <van-submit-bar :price="($store.getters.shu)*100" button-text="提交订单">
            <input type="checkbox" :checked="allChecked" @click="addquan" />
            <el-button type="primary" round @click="del">选中删除</el-button>
        </van-submit-bar>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            count: 0,
            isLoading: false,
        };
    },
    created() {

    },
    mounted() {

    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                Toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 1000);
        },
        // 增加一个
        addshop(i){
            this.$store.commit('addshop',i)
        },
        // 减去一个商品
        open(i) {
            this.$store.commit('jian',i)
        },
        // 单选框
        addxuan(i){
            this.$store.commit('check',i)
        },
        // 全选框
        addquan(){
            this.$store.commit('addBox')
        },
        // 选中删除
        del(i){
            this.$store.commit('quandel',i)
        }
    },
    computed: {
        shop(){
            return this.$store.state.cart
        },
        allChecked(){
            return this.$store.state.allChecked
        }
    }
};
</script>

<style scoped lang="scss">
    .ol .van-button{
        height: 30px;
        width: 30px;
    }
    .shopCart{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .el-button{
        margin-left: 5px;
    }
    input{
        width: 15px;
        height: 15px;
    }
    header{
        width: 100%;
        height: 50px;
        display: flex;
        align-items: center;
        line-height: 50px;
        background: crimson;
        justify-content: space-between;
        color: #fff;
        .van-icon{
            font-size: 25px;
        }
    }
    .van-pull-refresh{
        flex: 1;
        overflow: auto;
    }
    .van-pull-refresh::-webkit-scrollbar { display: none }
    .ol{
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
        li{
            width: 100%;
            height: 120px;
            background: #FAFAFA;
            margin-bottom: 5px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            img{
                width: 70px;
                height: 70px;
            }
            .tet{
                width: 180px;
                .list-p1{
                    width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: 14px;
                }
                .list-p2{
                    color: crimson;
                    font-size: 18px;
                }
            }
        }
    }
</style>
