<template>
  <div class="search">
    <header>
      <van-icon name="arrow-left" @click="$router.push('/home/leader')"/>      
      <van-search v-model="value" placeholder="请输入搜索关键词" @keydown.enter="search()"/>
      <div> </div>
    </header>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <div class="tag">
            <p>
              历史记录
              <i class="el-icon-delete" @click="clear()"></i>
            </p>    
            <el-tag v-for="(item,index) in history" :key="index">{{ item.title }}</el-tag>    
        </div>
        <!-- {{ arr }} -->
        <div>
            <ul class="ul">
                <li v-for="(item,index) in arr" :key="index">
                  <el-tag type="danger">{{ item.title }}</el-tag>
                </li>    
            </ul>    
        </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data() {
    return {
        value:'',
        count: 0,
        isLoading: false,
        searchList:[],
        arr:[],
        history:JSON.parse(localStorage.getItem('cart'))||[]
    };
  },
  created() {},
  mounted() {
     this.$axios.get('/list.json').then((res)=>{
         this.searchList = res.data.result    
     })     
  },
  methods: {
     onRefresh() {
      setTimeout(() => {
        Toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
    search(){
        // 搜索
        this.arr = []
        this.searchList.forEach((item)=>{
            if(item.title.includes(this.value)){
                this.arr.push(item)  
            }    
        })
        //历史记录
        let obj = {"title":this.value}
        if(this.history.length<5){
            this.history.unshift(obj)    
        }else{
            this.history.pop()
            this.history.unshift(obj)    
        }
        localStorage.setItem('cart',JSON.stringify(this.history))
    },
    clear(){
      this.history = []
      localStorage.setItem('cart',JSON.stringify(this.history))
    }
  },
  computed: {
         
  }
};
</script>

<style scoped lang="scss">
.search{
   width: 100%;
   height: 100%;
   display: flex;
   flex-direction: column;  
}
header{
   width: 100%;
   height: 54px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   box-sizing: border-box;
   background: brown;
   padding: 0 10px;
   .van-icon{
      font-size: 25px;
      color: #fff;   
   }
}
.van-search{
   width: 85%;
   background: brown;    
}
.van-pull-refresh{
   flex: 1;
   overflow: auto;     
}
.tag{
   width: 100%;
   height: 70px;
   p{
      margin: 5px;
      display: flex;
      color: cadetblue;
      font-size: 14px;
      align-items: center;
      .el-icon-delete{
        margin-left: 300px;
        font-size: 18px;
        color: crimson;
      }
   }
   .el-tag{
      margin: 5px;     
   }     
}
.ul{
  width: 100%;
  li{
    width: 100%;
    .el-tag{
      width: 90%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin: 5px;
    }
  }
}
.van-pull-refresh::-webkit-scrollbar { display: none }
</style>
