import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Category from '../ZHome/Category'
import Search from '../ZHome/Search'
import Myorder from '../ZHome/Myorder'
import Cart from '../ZHome/Cart'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect: "home",
  },
  {
    path:'/home',
    name:'Home',
    component:Home,
    children:[
      {
        path:'/',
        redirect: "/home/leader"
      },
      {
        path:'/home/leader',
        name:'Leader',
        component: function () {
          return import(/* webpackChunkName: "about" */ '../ZHome/Leader.vue')
        }
      },
      {
        path:'/home/category',
        name:'Category',
        component:Category
      },
      {
        path:'/home/search',
        name:'Search',
        component:Search
      },
      {
        path:'/home/myorder',
        name:'Myorder',
        component:Myorder
      },
      {
        path:'/home/cart',
        name:'Cart',
        component:Cart
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/About.vue')
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
